.can-help {
  padding-bottom: 108px;
  position: relative;
}

.title-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  a:active,
  a:hover,
  a {
    text-decoration: none;
    color: #000000;
    cursor: auto;
    border-top: 80px solid transparent;
    margin-top: -80px;
    padding: 15px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
  }

  h2 {
    display: block;
    @include fluidFontSize(30px, 40px, 480px, 1280px, 35px);
    font-weight: 500;
    line-height: 120%;
  }

  div {
    text-align: center;
    width: 710px;
    margin-bottom: 64px;
  }
}

.can-help__first-block {
  display: flex;
  justify-content: space-between;
  gap: 10%;
  margin-bottom: 104px;

  .donat-block {
    flex: 1 0 40%;

    h3 {
      @include fluidFontSize(16px, 20px, 480px, 1280px, 18px);
      margin: 0;
      padding: 0;
    }
  }

  .img-block {
    flex: 1 0 40%;

    img {
      width: 100%;
      height: 100%;
    }
  }
}

.donat-block__description {
  div {
    margin: 15px 0px;
  }
}

.bank-information-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  align-content: center;
  padding: 0px;
  gap: 48px;

  width: 1240px;
}

.bank-information-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
  gap: 24px;
  width: 381.33px;

  section {
    text-align: center;
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    position: relative;
  }
}

.text_input {
  box-sizing: border-box;
  width: 498px;
  height: 56px;
  padding-left: 20px;
  overflow: hidden;
  border: none;
  border-bottom: 2px solid black;
  margin-bottom: 28px;
}

.text_input::-webkit-outer-spin-button,
.text_input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.text_input {
  appearance: none;
  -moz-appearance: textfield;
  outline: none;
}

.text_input:hover {
  appearance: none;
  -moz-appearance: textfield;
  background-color: rgba(31, 30, 27, 0.1);
  border: none;
  border-bottom: 2px solid #0275ed;
}

.text_input:focus {
  appearance: none;
  -moz-appearance: textfield;
  border: none;
  border-bottom: 4px solid #0275ed;
}

.text_input__disabled {
  box-sizing: border-box;
  width: 498px;
  height: 56px;
  padding-left: 20px;
  overflow: hidden;
  border: none;
  border-bottom: 2px solid red;
  margin-bottom: 28px;
}

.text_input__disabled::-webkit-outer-spin-button,
.text_input__disabled::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.text_input__disabled::placeholder {
  color: red;
}

.text_input__disabled {
  appearance: none;
  -moz-appearance: textfield;
  outline: none;
}

.text_input__disabled:hover {
  appearance: none;
  -moz-appearance: textfield;
  background-color: rgba(31, 30, 27, 0.1);
  border: none;
  border-bottom: 2px solid red;
}

.text_input__disabled:focus {
  appearance: none;
  -moz-appearance: textfield;
  border: none;
  border-bottom: 4px solid red;
}

.can-help__second-block {
  width: 382px;
  display: flex;
  flex-direction: column;

  h3 {
    margin-bottom: 72px;
  }
}

.table-block__name-company {
  display: flex;
  gap: 3px;
  width: 382px;
  height: 68px;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  border-bottom: 1px solid black;
}

.table-block__title {
  width: 100%;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 130%;
  width: 99px;
}

.table-block__name {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 130%;
  text-align: left;
  width: 227px;
}

.table-block__image {
  width: 48px;
  height: 48px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    border-radius: 48px;
    background-color: rgba(31, 30, 27, 0.1);
  }

  &:active {
    border-radius: 48px;
    background-color: #FFD966;
  }
}

.table-block {
  width: 382px;
  position: relative;
}

.duble-table-block {
  display: flex;
  gap: 4%;
}

.donat-block__first-button-block {
  display: flex;
  font-size: 14px;
  color: #1d192b;
  margin-bottom: 20px;
  width: 240px;
  height: 40px;

  :nth-child(2) {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    gap: 8px;
    width: 80px;
    height: 40px;
    border: 1px solid #000000;
    border-radius: 100px 0px 0px 100px;
    cursor: pointer;

    &:hover {
      background-color: rgba(31, 30, 27, 0.1);
    }
  }

  :nth-child(4) {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    gap: 8px;
    width: 80px;
    height: 40px;
    cursor: pointer;
    border: 1px solid #000000;

    &:hover {
      background-color: rgba(31, 30, 27, 0.1);
    }
  }

  :last-child {
    display: flex;
    gap: 8px;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 80px;
    height: 40px;
    border: 1px solid #000000;
    border-radius: 0px 100px 100px 0px;
    cursor: pointer;

    &:hover {
      background-color: rgba(31, 30, 27, 0.1);
    }
  }
}

input[type='radio'][name='can-help-button'] {
  display: none;
}

input[type='radio'][name='can-help-button']+label {
  :nth-child(2) {
    all: revert;
  }

  :first-child {
    all: revert;
    display: none;
  }
}

input[type='radio'][name='can-help-button']:checked+label {
  >* {
    all: revert;
  }

  :first-child {
    all: revert;
    display: block;
    border: 0px;
    width: 9px;
    height: 7.5px;
  }

  background-color: rgba(31, 30, 27, 0.1);
}

input[type='radio'][name='can-help-button2'] {
  display: none;
}

input[type='radio'][name='can-help-button2']+label {
  :first-child {
    all: revert;
    display: none;
  }

  :nth-child(2) {
    all: revert;
  }
}

input[type='radio'][name='can-help-button2']:checked+label {
  :nth-child(2) {
    all: revert;
  }

  :first-child {
    all: revert;
    display: block;
    border: 0px;
    width: 9px;
    height: 7.5px;
  }

  background-color: rgba(31, 30, 27, 0.1);
}

