.table-block__pop-up {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;

  width: 300px;
  height: 48px;

  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;

  background: rgba(31, 30, 27, 0.1);
  border-radius: 44px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;

  display: flex;
  align-items: center;
  text-align: center;
}

.input-error-currency {
